*{
  margin: 0%;
  padding: 0%;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Zeyada';
  src: url('../fonts/Zeyada-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'Flow Circular';
  src: url('../fonts/FlowCircular-Regular.ttf');
}
@font-face {
  font-family: 'P2P';
  src: url('../fonts/PressStart2P-Regular.ttf') format('truetype');
}
@font-face {
  font-family: 'Prompt';
  src: url('../fonts/Prompt-Regular.ttf') format('truetype');
}
@keyframes colorChange {
  0% {
    color: #ef3550;
  }
  11% {
    color: #f48fb1;
  }
  22% {
    color: #7e57c2;
  }
  33% {
    color: #2196f3;
  }
  44% {
    color: #26c6da;
  }
  55% {
    color: #43a047;
  }
  66% {
    color: #eeff41;
  }
  77% {
    color: #f9a825;
  }
  88% {
    color: #ff5722;
  }
  100% {
    color: #ef3550;
  }
}
@keyframes shadowChange {
  0% {
    text-shadow: -4px 4px #ff5722, -8px 8px #ef3550, -12px 12px #f48fb1, -16px 16px #7e57c2, -20px 20px #2196f3, -24px 24px #26c6da, -28px 28px #43a047, -32px 32px #eeff41, -36px 36px #f9a825;
  }
  11% {
    text-shadow: -4px 4px #f48fb1, -8px 8px #7e57c2, -12px 12px #2196f3, -16px 16px #26c6da, -20px 20px #43a047, -24px 24px #eeff41, -28px 28px #f9a825, -32px 32px #ff5722, -36px 36px #ef3550;
  }
  22% {
    text-shadow: -4px 4px #7e57c2, -8px 8px #2196f3, -12px 12px #26c6da, -16px 16px #43a047, -20px 20px #eeff41, -24px 24px #f9a825, -28px 28px #ff5722, -32px 32px #ef3550, -36px 36px #f48fb1;
  }
  33% {
    text-shadow: -4px 4px #2196f3, -8px 8px #26c6da, -12px 12px #43a047, -16px 16px #eeff41, -20px 20px #f9a825, -24px 24px #ff5722, -28px 28px #ef3550, -32px 32px #f48fb1, -36px 36px #7e57c2;
  }
  44% {
    text-shadow: -4px 4px #26c6da, -8px 8px #43a047, -12px 12px #eeff41, -16px 16px #f9a825, -20px 20px #ff5722, -24px 24px #ef3550, -28px 28px #f48fb1, -32px 32px #7e57c2, -36px 36px #2196f3;
  }
  55% {
    text-shadow: -4px 4px #43a047, -8px 8px #eeff41, -12px 12px #f9a825, -16px 16px #ff5722, -20px 20px #ef3550, -24px 24px #f48fb1, -28px 28px #7e57c2, -32px 32px #2196f3, -36px 36px #26c6da;
  }
  66% {
    text-shadow: -4px 4px #eeff41, -8px 8px #f9a825, -12px 12px #ff5722, -16px 16px #ef3550, -20px 20px #f48fb1, -24px 24px #7e57c2, -28px 28px #2196f3, -32px 32px #26c6da, -36px 36px #43a047;
  }
  77% {
    text-shadow: -4px 4px #f9a825, -8px 8px #ff5722, -12px 12px #ef3550, -16px 16px #f48fb1, -20px 20px #7e57c2, -24px 24px #2196f3, -28px 28px #26c6da, -32px 32px #43a047, -36px 36px #eeff41;
  }
  88% {
    text-shadow: -4px 4px #ff5722, -8px 8px #ef3550, -12px 12px #f48fb1, -16px 16px #7e57c2, -20px 20px #2196f3, -24px 24px #26c6da, -28px 28px #43a047, -32px 32px #eeff41, -36px 36px #f9a825;
  }
  100% {
    text-shadow: -4px 4px #ef3550, -8px 8px #f48fb1, -12px 12px #7e57c2, -16px 16px #2196f3, -20px 20px #26c6da, -24px 24px #43a047, -28px 28px #eeff41, -32px 32px #f9a825, -36px 36px #ff5722;
  }
}