.text-container{
    display: flex;
    padding: 40px;
    flex-wrap: wrap;
    font-size: 4.6vw;
    gap: 10px;
}
.item{
    font-family: 'P2P';
    color:white;
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00
}
.item-selected{
    font-family: 'P2P';
    color:white;
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    animation: light-off 3s linear infinite;
}

@keyframes light-off {
    0%, 43%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }
    44%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }46%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }47%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }
    100%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }


}
