*{
  margin: 0%;
  padding: 0%;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Zeyada';
  src: url(/static/media/Zeyada-Regular.9db70005.ttf) format('truetype');
}
@font-face {
  font-family: 'Flow Circular';
  src: url(/static/media/FlowCircular-Regular.e0684342.ttf);
}
@font-face {
  font-family: 'P2P';
  src: url(/static/media/PressStart2P-Regular.1ce708e1.ttf) format('truetype');
}
@font-face {
  font-family: 'Prompt';
  src: url(/static/media/Prompt-Regular.c11ed9eb.ttf) format('truetype');
}
@-webkit-keyframes colorChange {
  0% {
    color: #ef3550;
  }
  11% {
    color: #f48fb1;
  }
  22% {
    color: #7e57c2;
  }
  33% {
    color: #2196f3;
  }
  44% {
    color: #26c6da;
  }
  55% {
    color: #43a047;
  }
  66% {
    color: #eeff41;
  }
  77% {
    color: #f9a825;
  }
  88% {
    color: #ff5722;
  }
  100% {
    color: #ef3550;
  }
}
@keyframes colorChange {
