.App {
  text-align: center;
  padding: 0%;
  margin:0%;
  background: linear-gradient( #0c031da6, #5a5368ef );
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header img{
  height: 50vh;
  border-radius: 100%;
  background: transparent;
  border-color: #000000;
}

.App-header button {
  border-radius: 100%;
  border-color: transparent;
  background-color: #0a053596;
}
.repository-list-container {
    display: flex;
    position: absolute;
    padding-bottom: 5vw;
    background: transparent;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    margin-top: 10px;
    height: 21vw; 
    
    @media screen and (min-width: 768px) {
        flex-direction: row;
    }
}

.repo-list {
    flex: 1 1;
    align-items: center;
    justify-self: end;
    font-size: 2vw;
    padding: 1.2vw;
  
    @media screen and (min-width: 768px) {
      width: 50%;
    }
}

.repository-item {
    border: none;
    background: none;
    padding: 1vw;
    margin: 0;
    text-decoration: none;
    cursor: pointer;

    color: white;
    
    font-family: 'P2P';
}
  
.repository-item.selected {
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    -webkit-animation: glow 2s linear infinite;
            animation: glow 2s linear infinite;
}

.selected-repo-container{
    font-family: 'Prompt';
    
    flex: 1 1;
    flex-basis: 9%;
    margin-bottom: 20px;
    margin-right: 10vw;
    margin-left: 0;
    
    
    cursor: default;
    
    @media screen and (min-width: 768px) {
        width: 50%;
    }
}

.selected-repo{
    padding:0%;
}

.selected-repo p{
    justify-self: right;
    text-align: left;
    padding: 10px ;
    color: white;
    
    font-size: 1.6vw;
}

.btn-git-url{
    border-radius: 2vw;
    background:#1a031d8f;

    height: 50px;
    width: 200px;
    
    cursor:pointer;

    border-color: transparent;

    
    align-items: center;
    justify-content: center;
}

.selected-repo a{
    text-decoration: none;
    text-align: center;
    align-self: center;
    justify-self:center;
    width: 5px;
    
    color : white;

    font-size: 1.2vw;
    font-weight: bold;
    font-style: italic;
}

.initial-message{
    color: white;
    font-size: 2vw;

}

@-webkit-keyframes glow {
    100% {
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }   
}

@keyframes glow {
    100% {
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
    }   
}

.container{
    display: flex;
    flex-direction:  column;
    padding: 5vw;
    color: white;
}

.margin-container{
    padding: 2vw;
}

.margin-container h2{
    font-family: 'P2P';
    margin: 5px;
    padding-bottom: 50px;
    -webkit-animation: colorChange 2s infinite, shadowChange 1s infinite;
            animation: colorChange 2s infinite, shadowChange 1s infinite;
}

.margin-container p{
    font-size:larger;
}
.footer {
    background: #08021388;
    display: grid;
    position: relative;
    padding: 50px;
    color: white;
    border-top-left-radius: 30vw;
    border-top-right-radius: 30vw;
    grid-gap: 4vw;
    gap: 4vw;
}
  
.name {
    position: absolute;
    left: 35vw;
    top: 0;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 5vw;
    font-family: 'Zeyada';
    font-size: 7vw;
    text-align: center;
}
  
.list-url-container {
    position: absolute;
    text-align: center;
    list-style: none;
    margin: 0;
    left: 52vw;
    top:9vw;
}

.list-url-container a {
    position:relative;
    padding: 3vw;
    text-decoration: none;
    font-family: 'Flow Circular';
    font-size: 1.2vw;
    transition: margin-left 0.3s ease-in-out;
}
  
.list-url-container a:hover {
    margin-left: 10px;
    font-family: 'Prompt';
}
  
.derechos-reservados {
    position: relative;
    padding: 4vw;
    top: 4vw;
    color: #ffffff28;
    text-align: center;
}
  
.text-container{
    display: flex;
    padding: 40px;
    flex-wrap: wrap;
    font-size: 4.6vw;
    grid-gap: 10px;
    gap: 10px;
}
.item{
    font-family: 'P2P';
    color:white;
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00
}
.item-selected{
    font-family: 'P2P';
    color:white;
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    -webkit-animation: light-off 3s linear infinite;
            animation: light-off 3s linear infinite;
}

@-webkit-keyframes light-off {
    0%, 43%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }
    44%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }46%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }47%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }
    100%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }


}

@keyframes light-off {
    0%, 43%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }
    44%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }46%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }47%{
        text-shadow: 0 0 15px #000000, 0 0 25px #000000;
    }
    100%{
        text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    }


}

