.App {
  text-align: center;
  padding: 0%;
  margin:0%;
  background: linear-gradient( #0c031da6, #5a5368ef );
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-header img{
  height: 50vh;
  border-radius: 100%;
  background: transparent;
  border-color: #000000;
}

.App-header button {
  border-radius: 100%;
  border-color: transparent;
  background-color: #0a053596;
}