.footer {
    background: #08021388;
    display: grid;
    position: relative;
    padding: 50px;
    color: white;
    border-top-left-radius: 30vw;
    border-top-right-radius: 30vw;
    gap: 4vw;
}
  
.name {
    position: absolute;
    left: 35vw;
    top: 0;
    transform: translateX(-50%);
    padding: 5vw;
    font-family: 'Zeyada';
    font-size: 7vw;
    text-align: center;
}
  
.list-url-container {
    position: absolute;
    text-align: center;
    list-style: none;
    margin: 0;
    left: 52vw;
    top:9vw;
}

.list-url-container a {
    position:relative;
    padding: 3vw;
    text-decoration: none;
    font-family: 'Flow Circular';
    font-size: 1.2vw;
    transition: margin-left 0.3s ease-in-out;
}
  
.list-url-container a:hover {
    margin-left: 10px;
    font-family: 'Prompt';
}
  
.derechos-reservados {
    position: relative;
    padding: 4vw;
    top: 4vw;
    color: #ffffff28;
    text-align: center;
}
  