.repository-list-container {
    display: flex;
    position: absolute;
    padding-bottom: 5vw;
    background: transparent;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
    margin-top: 10px;
    height: 21vw; 
    
    @media screen and (min-width: 768px) {
        flex-direction: row;
    }
}

.repo-list {
    flex: 1;
    align-items: center;
    justify-self: end;
    font-size: 2vw;
    padding: 1.2vw;
  
    @media screen and (min-width: 768px) {
      width: 50%;
    }
}

.repository-item {
    border: none;
    background: none;
    padding: 1vw;
    margin: 0;
    text-decoration: none;
    cursor: pointer;

    color: white;
    
    font-family: 'P2P';
}
  
.repository-item.selected {
    text-shadow: 0 0 15px #ffff00, 0 0 25px #ffff00;
    animation: glow 2s linear infinite;
}

.selected-repo-container{
    font-family: 'Prompt';
    
    flex: 1;
    flex-basis: 9%;
    margin-bottom: 20px;
    margin-right: 10vw;
    margin-left: 0;
    
    
    cursor: default;
    
    @media screen and (min-width: 768px) {
        width: 50%;
    }
}

.selected-repo{
    padding:0%;
}

.selected-repo p{
    justify-self: right;
    text-align: left;
    padding: 10px ;
    color: white;
    
    font-size: 1.6vw;
}

.btn-git-url{
    border-radius: 2vw;
    background:#1a031d8f;

    height: 50px;
    width: 200px;
    
    cursor:pointer;

    border-color: transparent;

    
    align-items: center;
    justify-content: center;
}

.selected-repo a{
    text-decoration: none;
    text-align: center;
    align-self: center;
    justify-self:center;
    width: 5px;
    
    color : white;

    font-size: 1.2vw;
    font-weight: bold;
    font-style: italic;
}

.initial-message{
    color: white;
    font-size: 2vw;

}

@keyframes glow {
    100% {
        filter: hue-rotate(360deg);
    }   
}
